body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

.stock-container {
  background: #fcfcfc;
  border: 1px solid #eee;
  margin: 0 auto;
  width: 600px;
}

.stock-container--header {
  background: #333;
  color: #fff;
  font-size: 18px;
  font-weight: 500;
}

.stock-container__name,
.stock-container__value {
  border-right: 1px solid #ccc;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  display: inline-block;
  padding: 20px;
  width: 25%;
}

.stock-container--green {
  background-color: #55ac20;
  color: #fff;
}

.stock-container--red {
  background-color: #fa1919;
  color: #fff;
}

.stock-container__last-update {
  display: inline-block;
  font-size: 14px;
  font-weight: 300;
  width: 50%;
}

.stock-container__last-update--header {
  font-size: 18px;
  font-weight: 500;
}



